import PropTypes from "prop-types";
import styled, { ThemeProvider } from "styled-components";

const Button = styled.button`
  background-color: ${(props) => props.theme.buttonBgColor};
  color: ${(props) => props.theme.buttonFgColor};
  border-radius: 3px;
  border: 1px solid ${(props) => props.theme.buttonBgColor};
  padding: 10px;
  font-size: 1.2em;

  &:hover {
    background-color: #ffb142;
    color: black;
    border-color: ${(props) => props.theme.activeBgColor};
    cursor: pointer;
  }
`;

const List = styled.ul`
  list-style-type: none;
  padding-left: 0px;

  & li {
    display: inline;
  }

  & li.disabled {
    display: inline;
    cursor: default;
  }

  & li.disabled button {
    color: black;
    background-color: lightgray;
    border-color: lightgray;
    cursor: default;
  }

  & li.active button {
    background-color: ${(props) => props.theme.activeBgColor};
    border-color: ${(props) => props.theme.activeBgColor};
    color: ${(props) => props.theme.activeFgColor};
    cursor: default;
  }
`;

const getListItems = (props) => {
  const { selectedPage, numberOfPages } = props;

  let list = [];
  const lastPage = selectedPage === numberOfPages;
  const firstPage = selectedPage === 1;

  let nextPage = lastPage ? selectedPage : selectedPage + 1;

  let previousPage = firstPage ? selectedPage : selectedPage - 1;

  list.push(
    <li key={`prev-page`} className={`${firstPage ? "disabled" : ""}`}>
      <Button onClick={() => props.onSelectPage(previousPage)}>Previous</Button>
    </li>
  );

  for (let i = 1; i <= props.numberOfPages; i++) {
    list.push(
      <li
        key={`page${i}`}
        className={`${i === selectedPage ? "active" : ""} ml-1`}
      >
        <Button onClick={() => props.onSelectPage(i)}>{i}</Button>
      </li>
    );
  }

  list.push(
    <li key={`next-page`} className={`${lastPage ? "disabled" : ""} ml-1`}>
      <Button onClick={() => props.onSelectPage(nextPage)}>Next</Button>
    </li>
  );

  return list;
};

const Pagination = (props) => {
  return (
    <ThemeProvider theme={props.theme}>
      <List>{getListItems(props)}</List>
    </ThemeProvider>
  );
};

Pagination.propTypes = {
  selectedPage: PropTypes.number.isRequired,
  numberOfPages: PropTypes.number.isRequired,
  onSelectPage: PropTypes.func.isRequired,
};

export default Pagination;
